@import url(https://fonts.googleapis.com/css2?family=Arimo&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  background: rgb(182, 177, 171);
  width: 100%;
  
}


















.App {
  background-color: rgb(187, 176, 176);
}

.Header {
    width: 100%;
    min-height: 150px;
    font-family: 'Arimo', sans-serif;
    background-color: rgb(240, 230, 230);
  }
  .Header h1 {
    padding-top: 3em;
    padding-left: 3em;
    font-weight: 900;
  }
.Nav {
    width: 100%;
    background: rgba(187, 176, 176, 0.67);
    min-height: 50px;
    text-align: right;
    position: fixed;
    z-index: 3;
    top: 25px;
    padding-top: 11px;
    font-size: 1.1em;
    font-family: 'Arimo', sans-serif;
  }
  .Nav a {
    text-decoration: none;
    color: #333;
    margin: 0 1.2em;
  }
  .Nav a:hover {
    color:rgb(55, 21, 180);
  }
  button {
    font-size: 0.8em;
    padding: 0.2em;
    margin: 0.1em;
    border-radius: 0.3em;
    border: 0.1em solid #333;
    background-color: rgb(110, 233, 130);
    width: 50%;
    cursor: pointer ;
    -webkit-align-content: center;
            align-content: center;
    text-align: center;
  }
  button.count {
    background-color: darksalmon;
    border: none;
    color: #fff;
    width: 3em;
    margin-right: 3em;
  }
.Home {
    width: 100%;
    min-height:325px;
    text-align: center;
    background-color: rgb(187, 176, 176);
  }
  .Home h1 {
    text-align: center;
    padding-top: 1em;
    font-size: 4em;
  }
  .Home p {
    text-align: center;
    font-size: 1em;
    padding-top: 2em;
    padding-bottom: 2em;
  }
  .Home Button {
    width: 10em;
  }
.Contact {
    width: 100%;
    min-height:325px;
    text-align: center;
    background-color: rgb(187, 176, 176);
  }
  .Contact h1 {
    text-align: center;
    padding-top: 2.3em;
    font-size: 2em;
  }
  .phone {
    text-align: center;
    font-size: 1em;
    padding-top: 3em;
  }
  .email {
    text-align: center;
    font-size: 1em;
    padding-top: 3em;
    padding-left: 7em;
  }
.pic {
    background-color: rgb(240, 230, 230);
    float: left;
    margin: 4%;
    margin-left: 7%;
    text-align: center;
    border: 3px solid rgb(102, 95, 95);
    border-radius: 1em;
  }
  .pic h3 {
    font-size: 1.1em;
    font-weight: 600;
  }
  .image {
    height: 8em;
    width: 10em;
    background-color: #fff;
    border: 3px;
    border-radius: 1em 1em 0 0; 
  }
.Main {
    width: 100%;
    min-height: 700px;
    font-family: 'Arimo', sans-serif;
    background-color: rgb(187, 176, 176);
  }
  .Main >h2 {
    color:#333;
    padding-left: 4em;
    padding-top: 1em;
  }
  .productMap {
    width: 100%;
  }
.Cart {
    width: 100%;
    min-height: 325px;
    background: rgb(247, 170, 104);
    font-family: 'Arimo', sans-serif;
  }
  .Cart h2{
    font-size: 1.2em;
    font-weight: 500;
    color: #000;
    padding-top: 50px;
    padding-left: 3em;
  }
  .Cart h3{
    font-weight: 400;
    color: #000;
    padding-top: 30px;
    padding-left: 3em;
  }
  .text {
    text-align: right;
    padding-right: 6em;
  }
  .itemName {
    padding-left: 2em;
    padding-right: 2em;
    padding-top: 30px;
    padding-bottom: 1px;
  }
  button {
    font-size: 0.8em;
    padding: 0.2em;
    margin: 0.1em;
    border-radius: 0.3em;
    border: 0.1em solid #333;
    background-color: rgb(110, 233, 130);
    width: 50%;
    cursor: pointer ;
    -webkit-align-content: center;
            align-content: center;
    text-align: center;
  }
  button.remove {
    background-color: rgb(231, 71, 71);
    width: 3.5em;
    margin-left: 5em;
    margin-top: 15px;
  }
  .inside{
    padding-left: 2em;
  }
  .checkOut {
    width: 40%;
    -webkit-justify-content: center;
            justify-content: center;
    margin-left: 40%;
  }
.signin  {
    min-height: 325px;
    text-align: center;
    padding-top: 100px;
    background-color: rgb(187, 176, 176);
  }
  .signin h1 {
    padding-bottom: 50px;
  }
  .signin button {
    width: 10%;
    background-color: rgb(126, 153, 80);
  }
.Footer {
    width: 100%;
    min-height: 50px;
    padding-bottom: 1.6em;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content:center;
            justify-content:center;
    background-color: #000;
    color: #fff;
    display:-webkit-flex;
    display:flex;
    font-family: 'Arimo', sans-serif;
  }
  
.social-container {
    background: #000;
    padding: 25px 50px;
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
  }
  a.social {
    margin: 0 1.6em;
    transition: -webkit-transform 250ms;
    transition: transform 250ms;
    transition: transform 250ms, -webkit-transform 250ms;
    display: inline-block;
  }
  a.social:hover {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
  }
  a.youtube {
    color: #eb3223;
  }
  
  a.facebook {
    color: #4968ad;
  }
  
  a.twitter {
    color: #49a1eb;
  }
  
  a.instagram {
     color: rgb(233, 72, 206)
  }
